'use client'

import { createContext, useContext, useState, ReactNode, useEffect } from 'react'

type AccessibilitySettings = {
  fontSize: 'small' | 'medium' | 'large' | 'xlarge'
  contrast: 'normal' | 'high' | 'blue'
  voiceEnabled: boolean
}

type AccessibilityContextType = {
  settings: AccessibilitySettings
  setSetting: (key: keyof AccessibilitySettings, value: any) => void
}

const defaultSettings: AccessibilitySettings = {
  fontSize: 'medium',
  contrast: 'normal',
  voiceEnabled: false
}

const AccessibilityContext = createContext<AccessibilityContextType>({
  settings: defaultSettings,
  setSetting: () => {}
})

export const AccessibilityProvider = ({ children }: { children: ReactNode }) => {
  const [settings, setSettings] = useState<AccessibilitySettings>(defaultSettings)

  // Load from localStorage
  useEffect(() => {
    const saved = localStorage.getItem('elderease_preferences')
    if (saved) setSettings(JSON.parse(saved))
  }, [])

  // Save to localStorage whenever settings change
  useEffect(() => {
    localStorage.setItem('elderease_preferences', JSON.stringify(settings))
  }, [settings])

  const setSetting = (key: keyof AccessibilitySettings, value: any) => {
    setSettings(prev => ({ ...prev, [key]: value }))
  }

  return (
    <AccessibilityContext.Provider value={{ settings, setSetting }}>
      {children}
    </AccessibilityContext.Provider>
  )
}

export const useAccessibility = () => useContext(AccessibilityContext)
